@font-face {
  font-family: "Robot";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Robot Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Robot";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Robot Medium"), url("../fonts/Roboto-Medium.woff") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "Robot";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Robot Bold"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
}
img {
  max-width: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.list {
  font-family: "Robot", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0 auto;
  min-width: 1200px;
  color: #000000;
  display: flex;
  gap: 50px;
  justify-content: center;
  align-items: center;
  padding: 50px 100px;
}
.list__item {
  width: 240px;
  height: 250px;
  padding: 25px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  background-color: #61c98b;
  color: #FFFFFF;
}
.list__wrap {
  display: flex;
  justify-content: space-between;
}
.list__image-holder {
  height: 65px;
  width: 65px;
  border-radius: 50%;
  background-color: rgba(88, 165, 119, 0.6);
}
.list__image-holder svg {
  position: relative;
  top: 16px;
  left: 18px;
}
.list__title {
  font-size: 36px;
  font-weight: normal;
  margin: 10px 0;
}
.list__item--level-two {
  background: #FFFFFF;
  color: #8d8d8d;
}
.list__image-holder--level-two {
  background-color: #FFFFFF;
}
.holder__title {
  font-family: "Robot", "Arial", sans-serif;
  font-size: 20px;
  line-height: 22px;
  font-weight: bold;
  text-align: right;
}
.holder__list {
  list-style: none;
  padding: 0;
  text-align: right;
  text-transform: capitalize;
  font-size: 16px;
}
.holder__label {
  display: flex;
  justify-content: right;
}
.holder__input--level-one {
  position: relative;
  z-index: -1;
}
.holder__checkmark {
  position: relative;
  right: 0;
  top: 0;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: 1.5px solid #FFFFFF;
  text-align: right;
  margin-left: 15px;
}
.holder__input--level-one:checked ~ .holder__checkmark {
  width: 30px;
  height: 30px;
  border: none;
  outline: none;
  background-image: url("../img/checked.svg");
  background-position: center;
  background-size: 24px;
  background-clip: border-box;
  background-repeat: no-repeat;
}
.holder__span {
  text-align: right;
}
.holder__list--level-two .holder__checkmark {
  position: relative;
  right: 0;
  top: 2px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1.8px solid #8d8d8d;
  text-align: right;
}
.holder__list--level-two {
  margin-bottom: 40px;
}
.holder__item {
  margin-bottom: 5px;
}
.holder__text {
  text-align: right;
  margin: 0;
}
.holder__text span {
  text-transform: uppercase;
}
